.myselfpage{
	width:750px;
	/*height:140px;*/
	background-color:#F5F5F5;
	
}
.myselftitle{
	width:750px;
	height:80px;
	background-color:#0085ff;
	text-align: center;
	line-height: 80px;
	position: relative;
	
}
.myselftitle>span{
	font-size:35px;
	color:white;
	font-weight: 700;
}
.myselftitle>span:nth-child(1){
	margin-left:0px;
	position: absolute;
	left:20px;
	
}
.myselftitle1{
	width:750px;
	height:200px;
	background-color:#0085ff;
	margin-top:-10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;

	
}
.myselftitle1>div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.myselftitle1 .userimg{
	width:120px;
	height:120px;
	border-radius: 50%;
	border:1px solid #ccc;
	margin-left:30px;
}
.myselftitle1 .userinfo{
	margin-left:35px;
	
}
.userinfo .username{
	color:white;
	font-size:50px;
	font-weight: 700px;;
}
.phone{
	font-size:23px;
	color:white;
	display: -ms-flexbox;
	display: flex;
}
.gopage{
	margin-right:20px;
	font-size:40px;
	color:white;
}
.myselfbody{
	width:750px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex:1 1;
	    flex:1 1;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	background:white;

}
.myselfbody>div{
	width:50%;
	height:168px;
	border:1px solid #ddd;
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;

	
}
.myselfbody>div:nth-child(2){
	margin-left:-3px;
}
.mybl>p:nth-child(1){
	color:rgb(255, 95, 62);
	font-size:60px;
	font-weight: 700;
}
.mybl>p:nth-child(2){
	color:#666;
	font-size:27px;
	font-weight: 700;
}
.mybl>p:nth-child(1)>span{
	color:rgb(255, 95, 62);
	font-size:20px;
}
.mybr>p:nth-child(1){
	color:rgb(106, 194, 11);
	font-size:60px;
	font-weight: 700;
}
.mybr>p:nth-child(1)>span{
	color:rgb(106, 194, 11);
	font-size:20px;
}
.mybr>p:nth-child(2){
	color:#666;
	font-size:27px;
	font-weight: 700;
}

.myselfpos{
	width:750px;
	height:90px;
	background:white;
	margin-top:20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size:30px;
	line-height: 90px;
	color:#bbb;
}
.myselfpos>div{
	margin-left:30px;
	margin-right:30px;
}
.myselfpos>div:nth-child(1){
	color:#333;
}


.myselfward{
	width:750px;
	height:180px;
	background:white;
	margin-top:20px;

	font-size:30px;
	line-height:90px;
	color:#bbb;
}
.myselfward>div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.myselfward>div:nth-child(1){
	border:1px solid #eee;
}
.myselfward>div>div{
	margin-left:30px;
	margin-right:30px;
}
.myselfward>div>div:nth-child(1){
	color:#333;
}


.myselfbottom{
	width:750px;
	height:180px;
	background:white;
	margin-top:20px;

	font-size:30px;
	line-height: 90px;
	color:#bbb;
}
.myselfbottom>div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.myselfbottom>div>div{
	margin-left:30px;
	margin-right:30px;
}
.myselfbottom>div:nth-child(1){
	border:1px solid #eee;
}
.myselfbottom>div>div:nth-child(1){
	color:#333;
}



