.homepage{
	width:750px;
	position: relative;
		
}
.homepage>div{
	width:750px;
	margin:0 auto;
	/*border:1px solid red;*/

}
.homepagebox{
	width:750px;
	height:250px;
}
.hometitle{
	width:750px;
	height:250px;
	background-color:#0af;
	position: fixed;
	top:0px;
}
.titletop{
	width:710px;
	margin:0 auto;
	height:100px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;	
}
.pos{
	margin-top:20px;	
}
.pos>span{
	font-size:35px;
	color:white;
}
.weather{
	margin-top:20px;
}
.weather>div>span{
	font-size:30px;
	color:white
}
.search{
	width:710px;
	height:70px;
	background-color:white;
	margin:-10px auto;
	font-size:30px;
	text-align: center;
	line-height: 70px;
	font-weight: 700;
	color:#777;
}
.titlebottom{
	width:710px;
	margin:20px auto;
	
}
.titlebottom>ul{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.titlebottom>ul>li{
	font-size:28px;
	color:white;
	margin-top:20px;
}
.homemain{
	width:750px;
}
.homemain1{
	width:750px;
	margin-top:20px;
	
}
.homemain1>ul{
	width:750px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	/*flex:1*/
}
.homemain1>ul>li{
	width:25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size:25px;
	color:#666;
}
.homemain1>ul>li>img{
	width:100px;
	height:100px;
}

.homemain2{
	width:710px;
	margin:0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top:40px;
}
.homemain2>div{
	width:50%;
	background-color:#f4f4f4;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	
}

.homemain2>div:nth-child(2){
	margin-left:20px;
}
.homemain2>div>span:nth-child(1){
	font-size:34px;
	color:#e81919;
	margin:0 20px;
	margin-top:30px;
	font-weight: 700;
}
.homepage .homemain2 .hotsell{
	color:#333;
}
.homepage .homemain2  .selltop{
	color:#af8260;
	font-weight: 700;
}
.homemain2>div>span:nth-child(2){
	font-size:26px;
	color:#777;
	margin-left:20px;
	margin-top:10px;
}
.homemain2>div>span:nth-child(3){
	font-size:25px;
	color:#444;
	margin-top:10px;
	font-weight: 700;
	margin-left:20px;
	
}
.homemain2>div>img{
	width:240px;
	height:170px;
	margin-left:100px;
	margin-top:20px;
}
.homemain3{
	width:750px;
	margin-top:20px;
	
}
.homemain3>ul{
	width:710px;
	margin:0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex:1 1;
	    flex:1 1;
}
.homemain3>ul>li{
	background-color:#f4f4f4;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	text-align: center;
	
}
.homemain3>ul>li:nth-child(1)>span:nth-child(1){
	font-size: 33px;
	font-weight: 700;
	margin-top:20px;
}
.homemain3>ul>li:nth-child(1)>span:nth-child(2){
	width:100px;
	font-size: 24px;
	color:#ff5339;
	border:1px solid #ffa89b;
	text-align: center;
		margin:0 auto;
	margin-top:10px;
}
.homemain3>ul>li:nth-child(2)>span:nth-child(1){
	font-size: 33px;
	font-weight: 700;
	margin-top:20px;
}
.homemain3>ul>li:nth-child(2)>span:nth-child(2){
	width:120px;
	font-size: 24px;
	color:#777;
	border:1px solid #bbb;
	text-align: center;
	margin:0 auto;
	margin-top:10px;
}
.homemain3>ul>li:nth-child(3)>span:nth-child(1){
	font-size: 33px;
	font-weight: 700;
	margin-top:20px;
}
.homemain3>ul>li:nth-child(3)>span:nth-child(2){
	width:120px;
	font-size: 24px;
	color:#777;
	border:1px solid #bbb;
	text-align: center;
	margin:0 auto;
	margin-top:10px;
}
.homebottom{
	margin-top:20px;
}
.homebottom .homeline{
	width:750px;
	margin:0 auto;
	font-size:35px;
	margin-top:30px;
	font-weight: 700;
	padding:20px;
	border-top:15px solid #eee;
	
}
/*.homeline{
	width:710px;
	margin:0 auto;
	border:2px solid  #eee;
}*/
.homemain{
	width:750px;
	/*height:1100px;*/
	/*overflow-x: scroll;*/
}
.bottomnav{
	width:750px;
	height:120px;
	position: fixed;
	bottom:0px;
	/*bottom:0px;*/
	border-top:2px solid #eee;
	background-color: white;
	border-top:1px solid #ccc;
}
.bottomnav>ul{
	width:750px;
	height:120px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex:1 1;
	    flex:1 1;
	text-align: center;
	/*line-height: 100px;*/
}
.bottomnav>ul>li{
	width:200px;
	height:70px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	/*border:1px solid red;*/
}
.bottomnav>ul>li>span:nth-child(1){
	font-size:50px;
	color:#666

}
.bottomnav>ul>li>span{
	font-size:25px;

}
.homeactive{
	color:#0089dc;
}



