.listpagetitle{
	width:750px;
	height:100px;
	background-color:skyblue;
	text-align:center;
	position: relative;
}
.listpagetitle>span{
	font-size:40px;
	line-height: 100px;
	color:white;
}
.listpagetitle>span:nth-child(1){
	position:absolute ;
	left:20px;
	font-size:50px;	
}
.listpageNav{
	width:750px;
	height:80px;
	line-height: 80px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	border-bottom: 1px solid #E8E8E8;
	-ms-flex:1 1;
	    flex:1 1;
	font-size:26px;	
	color:#666666;
	position: relative;
}
.sort{
	position: absolute;
	top:82px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width:750px;
	background-color: white;
	text-align: center;
	line-height: 100px;
}
.sort>span{
	height:100px;
	border-bottom:1px solid #eee;
}

.listshop{
	width:750px;
	margin-top:20px;
	
	border-bottom:1px solid #eee;	
}
.listshop>div{
	width:95%;
	margin:0 auto;
	display: -ms-flexbox;
	display: flex;
	margin-bottom:20px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	/*border:1px solid red;*/
	
}
.listshop .shopimg{
	width:120px;
	height:120px;
	/*border:1px solid #ccc;*/
	width:20%;
}
.listshoptop{
	width:78%;
	margin-left:25px;
}
.shopname{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}
.shopname>div{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.shopname .brand{
	width:60px;
	height:30px;
	font-size:20px;
	background-color: #fff100;
	text-align: center;
	line-height:30px;
	color:#6f3f15;
}
.shopname>div>span{
	font-size:30px;
	color:#333;
	margin-left:10px;
	font-family: "微软雅黑";
	font-weight: 700;
}
.shopname .align{
	text-align: center;
}
.shopname .align>span{
	width:35px;
	height:25px;
	font-size:18px;
	color:#999;
	border:1px solid #ddd;
}
.shopcomment{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top:10px;
}
.shopcomment>div:nth-child(1){
	font-size:22px;
	color:#444;
}
.shopcomment>div:nth-child(2){
	background-color:#0085ff;
	color:white;
	font-size:20px;
	height:24px;
	line-height: 24px;
}
.shopdetail{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top:10px;
}
.shopdetail>div>span{
	font-size:22px;
	color:#555;
	margin-left:5px;
}
.shopgood{
	color:red;
	font-size:22px;
	margin-top:10px;
}
.line{
	margin-top:10px;
	margin-bottom:10px;
	width:570px;
	border-top:1px  #E8E8E8 dashed;
}
.listshopbottom>div>span{
	font-size:20px;
	color:#444;
}
.listshopbottom>div{
	margin-top:8px;
	
}



