
.shopcarl{
	width:750px;
	background-color:#f5f5f5;

}
.sc{
	width:750px;
	/*height:1000px;*/
	overflow: scroll;
}
.titleover{
	height:120px;
	width:750px;
}
.shopcartitle{
	width:750px;
	background: #008eff;
	text-align: center;
	height:120px;
	line-height: 120px;
	position:fixed;
}

.shopcartitle>span:nth-child(1){
	font-size:50px;
	color:white;
	position: absolute;
	left:30px;
}
.shopcartitle>span:nth-child(2){
	font-size:50px;
	color:white;
}
.shopcarmain{
	width:750px;
	margin:0 auto;
	background:white;
	margin-bottom:20px;

}
.sc-shopname{
	height:100px;
	border-bottom:1px solid #ccc;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding:0 20px;
}
.sc-shopname>span{
	font-size:36px;
	font-weight: 700;
	line-height:100px;
}
.sc-shoplist{
	width:750px;
	padding-bottom:20px;
	border-bottom: 1px solid #ccc;	
}
.sc-shoplist>li{
	padding-bottom: 20px;
	width:710px;
	border-top: 1px solid #ccc;
	color:#333;
	margin:0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 1 1;
	    flex: 1 1;
}
.shopcarimg{
	width:129px;
	heigh:129px;
	margin-top:20px;
}
.sc-goodsname{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin-left:20px;
	width:600px;
	margin-top:20px;
}
.sc-goodsname .sr-gname{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.sr-gname>span{
	font-size:30px;
	color:#333;
}
.sc-goodsname .shoplistdetail{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top:40px;
}
.sc-goodsname .shoplistdetail>span{
	font-size:35px;
}
.shopcarpay{
	width:730px;
	height:100px;
	margin-top:20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}
 .shopcarpay>span:nth-child(1){
	font-size:35px;
	color:#333;
}
  .shopcarpay>span:nth-child(2){
	font-size:35px;
	color:orangered;
	margin-left:10px;
}
 .shopcarpay>span:nth-child(3){
	
	width:140px;
	height:60px;
	background: #29d96c;
	font-size:30px;
	color:white;
	line-height: 60px;
	margin-left:10px;
	padding:0 20px;
}
.carnull{
	width:100%;
	height:1150px;
	text-align: center;
	margin-top:60px;
}



