.shop{
	width:750px;
	height:100%;
	background:#f5f5f5;
}
.shoptitle{
	width:750px;
	background:white;	
	padding-left:30px;
	padding-top:30px;
	padding-bottom:30px;
}
.shoptitle>div{
	font-size:30px;
	color:black;
	font-weight: 700;
}
.shoptitle>ul{
	color:#666;
	font-size:23px;
	margin-top:20px;
}
.shoptitle>ul>li{
	padding:4px 0;
}
.shoptitle>ul>li>span{
	margin-right:10px;
}
.shoptitle>ul>li>span:nth-child(1){
	font-size:30px;
	color:rgb(241, 136, 79);
}
.shopcontent{
	margin-top:20px;
	background:white;
}
.shopcontent>div:nth-child(1){
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	border-bottom:1px solid #ccc;
	padding:30px;
	margin-top:20px;
}
.shopcontent>div:nth-child(1)>p{
	font-size:25px;
}
.shopcontent>div:nth-child(1)>p:nth-child(1){
	font-size:30px;
	font-weight: 700;
}
.shopcontent>div:nth-child(1)>p:nth-child(2){
	font-size:25px;
	font-weight: 700;
	color:#666;
	margin-top:20px;
}

.shopcontent>div:nth-child(2){
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	border-bottom:1px solid #ddd;
	padding:20px;
	padding-left:30px;
	font-size:30px;
}
.shopcontent>div:nth-child(2)>span:nth-child(1){
	margin-top:20px;
	font-size:30px;
}
.shopcontent>div:nth-child(2)>span:nth-child(2){
	margin-top:20px;
	font-size:30px;
	color:#666;
}

.shopcontent>div:nth-child(3){
	display: -ms-flexbox;
	display: flex;
	padding-left:30px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	border-bottom:1px solid #ddd;
	padding:20px;
	font-size:30px;
	padding-left:30px;
}
.shopcontent>div:nth-child(3)>span:nth-child(1){
	margin-top:20px;
	font-size:30px;
}
.shopcontent>div:nth-child(3)>span:nth-child(2){
	margin-top:18px;
	font-size:30px;
	color:#666;
}

.shopcontent>div:nth-child(4){
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	border-bottom:1px solid #ddd;
	padding:20px;
	font-size:30px;
		padding-left:30px;
}
.shopcontent>div:nth-child(4)>span:nth-child(1){
	margin-top:20px;
	font-size:30px;
}
.shopcontent>div:nth-child(4)>span:nth-child(2){
	margin-top:20px;
	font-size:30px;
	color:#666;
}

.shopbottom{
	margin-top:20px;
	background:white;
	height:100px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size:30px;
	line-height: 100px;
}
.shopbottom>span:nth-child(1){
	margin-left:30px;
}
.shopbottom>span:nth-child(2){
	margin-right:30px;
}



