.comment{
	width:750px;
	height:100%;
	background:#f5f5f5;
}
.commenttitle{
	width:750px;
	height:250px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	background:white;
}
.ctleft{
	width:40%;
	height:150px;
	border-right:1px solid #ccc;
	margin-top:40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	
}
.ctleft>p:nth-child(1){
	font-size:40px;
	color:#ff6000;
	
}
.ctleft>p:nth-child(2){
	font-size:30px;
	color:#666;
	
}
.ctleft>p:nth-child(3){
	font-size:20px;
	color:#999;
	
}
.ctright{
	width:60%;
	margin-left:40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: start;
	    align-items: flex-start;
}
.ctright>div{
	display: -ms-flexbox;
	display: flex;
	margin-bottom:10px;
	font-size:27px;
	color:#666;
}
.ctright>div>span{
	margin-right:15px;
}
.ctright>div>span:nth-child(3){
	color:#FF6000;
}
.ctright ul{
	display: -ms-flexbox;
	display: flex;
	margin-right: 15px;
}
.ctright ul li span{
	font-size:30px;
	color:#ffc600;
}
.commenttotal{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	background:white;
	margin-top:20px;
	padding:20px;
}
.commenttotal>div{
	width:150px;
	/*height:50px;*/
	background:#ebf5ff;
	margin-left:24px;
	margin-top:20px;
	font-size:24px;
	color:#6d7885;
	line-height: 50px;
	text-align: center;
}
.commenttotal>div:nth-child(1){
	background:#0097ff;
	color:white;
}
.commentcontent{
	width:750px;
	background:white;
	border-top:1px solid #ccc;
}
.commentcontent>div{
	width:680px;
	display: -ms-flexbox;
	display: flex;
	margin-left:40px;
	margin-top:50px;
	border-bottom:1px solid #ccc;
}
.ccright{
	margin-left:30px;
}
.ccright .ccuser>span:nth-child(1){
	font-size:34px;
	color:#333;
	font-weight: 700px;
}
.ccright .ccuser>span:nth-child(2){
	font-size:27px;
	color:#bbb;
	margin-left:20px;
}
.ccright .star{
	font-size:30px !important;
	color:orange;
	margin-top:10px;
	margin-bottom:10px;
}
.commentcontent .ccright  .ccbottom{
	width:710px;
	padding:20px;
	color:#6d7885 ;
	font-size:25px !important;

	margin-top:10px;
}



